import { Component, OnInit } from '@angular/core';
import { MenubarModule, MenuItem } from 'primeng/primeng';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
//订单
@Component({
  selector: 'app-Trad',
  templateUrl: './Trad.component.html',
  styleUrls: ['./Trad.component.scss'],
  providers: [Commfunc]
})
export class TradComponent implements OnInit {
  items1: MenuItem[];
  items2: MenuItem[];
  items3: MenuItem[];
  show: number;
  showClass: number = 0;
  cars: any = [
    {
      title: '订单查询',
      link: 'deal-center',
      id: 'SID02064_P0159',
      flag: 0
    },
    {
      title: '订单异常处理',
      link: 'exception-hand',
      id: 'SID02064_P0160',
      flag: 1
    },
    {
      title: '订单详情',
      link: 'order-details',
      id: '',
      flag: 2
    },
   ];
  constructor(public commfunc: Commfunc) { }

  ngOnInit() {
    this.items1 = [
      {
        label: '订单查询',
        routerLink: '/pages/tzb/Trad/deal-center'
      }
    ];
    this.items2 = [
      {
        label: '订单异常处理',
        routerLink: '/pages/tzb/Trad/exception-hand'
      }
    ];
    this.items3 = [
      {
        label: '订单详情',
        routerLink: '/pages/tzb/Trad/order-details'
      }
    ];
    // this.items3 = [
    //   {
    //     label: '订单异常处理',
    //     routerLink: '/pages/tzb/order/order-center/order-manage/order-exception-handle'
    //   }
    // ]
    this.judgStatu();
  }
  tab(flag) {
    // let target = event.target || event.toElement || event.srcElement
    // if (target) {
    //   // $(target).parent('div').addClass('active_item');
    //   // console.log(target);

    // }
    this.showClass = flag;
  }
  judgStatu() {
    let url = window.location.href;
    let urlSplit = url.split('/');
    urlSplit.map(v => {
      if (v === 'deal-center') {
        this.show = 1;
      } else if (v === 'exception-hand') {
        this.show = 2;
      }
      else if (v === 'order-details') {
        this.show = 3;
      }
      // else if (v === 'voucher-manage') {
      //   this.show = 3;
      // }else if (v === 'voucher-applicate') {
      //   this.show = 4;
      // }else if (v === 'voucher-applicate') {
      //   this.show = 5;
      // }
    })
  }
  apply(num) {
    this.show = num;
  }
  //按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }
}
