import { Component, OnInit } from '@angular/core';
import { ConfirmationService } from 'primeng/primeng';
import { Message } from 'primeng/primeng';
// import { OrderHttpService } from './../../../http/order.http.service';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { specialSymbol, checkNumber } from 'app/@uisftech/common/service/validator';
import { OrderQueryBean } from './bean/order-query.bean';
import { OrdersHttpService } from '../http/order.http.service';
// import { bankNo, salesChannelEnumId, tranType } from '../../../constant/codeValue';
@Component({
  selector: 'app-deal-center',
  templateUrl: './deal-center.component.html',
  styleUrls: ['./deal-center.component.scss'],
  providers: [ConfirmationService, OrdersHttpService, Commfunc]
})
export class DealCenterComponent implements OnInit {
  //表数据
  tableData: any = [];
  //下拉框
  label: any = [];//订单状态
  payStatus: any = [];//支付状态
  fulStatus: any = [];//交付状态
  bankNo: any = [];//银行标识
  salesChannelEnumId: any = [];//来源渠道
  tranType: any = [];//交易类型
  pageSize = 10;
  pageNumber = 1;
  // bean
  orderQueryBean: OrderQueryBean = new OrderQueryBean();
  //模态框
  headerTitle: string = "详情"; //模态框的标题
  display: boolean = false;  //模态框是否显示
  orgTreeDisplay: boolean = false;
  personPage: boolean = false;
  userInValue: any;
  ownerOrgInValue: any;
  showModel: string;
  //修改的参数
  updateValue: any = [];
  //分页
  total: any; //总页数
  first: number = 0;
  //提示信息
  msgs: Message[] = [];
  orderStatusId = [];
  orderTypeId = [
    { label: "请选择", value: "" },
    { label: "销售订单", value: "SALES_ORDER" },
    { label: "采购订单", value: "PURCHASE_ORDER" },
  ];
  transactionType = [
    { label: "请选择", value: "" },
    { label: "贷款发放", value: "FUNC000002" },
    { label: "贷款还款", value: "FUNC000004" },
    { label: "贷款借据结息", value: "FUNC000003" },
    { label: "应收利息回", value: "FUNC000006" },
    { label: "存款开户", value: "FUNC000007" },
    { label: "存款销户", value: "FUNC000008" },
    { label: "定期部提", value: "FUNC000009" },
  ];
  currencyUomIdss = [
    { label: "请选择", value: "" },
    { value: 'CNY', label: '人民币' },
    { value: 'USD', label: '美元' },
    { value: 'HKD', label: '港币' },
    { value: 'EUR', label: '欧元' },
    { value: 'JPY', label: '日元' },
    { value: 'GBP', label: '英镑' },
    { value: 'AUD', label: '澳大利亚元' },
    { value: 'CAD', label: '加拿大元' },
    { value: 'CHF', label: '瑞士法郎' },
    { value: 'DEM', label: '德国马克' },
    { value: 'SGD', label: '新加坡元' },
    { value: 'BEF', label: '比利时法郎' },
    { value: 'MOP', label: '澳门币' },
    { value: 'NLG', label: '荷兰盾' },
    { value: 'DKK', label: '丹麦克郎' },
    { value: 'SEK', label: '瑞典克朗' },
    { value: '98', label: '全外' },
    { value: '99', label: '全币种' },
    { value: 'NA', label: '缺省币种' },
  ];
  //订单支付状态	
  PaymentStatus = [
    { label: "请选择", value: "" },
    // { value: 'PAYMENT_RECEIVED', label: '付款已收到' },
    // { value: 'PAYMENT_NOT_RECEIVED', label: '付款未收到' },
    // { value: 'PAYMENT_CANCELLED', label: '支付已取消' },
    // { value: 'PAY_PART_CANCELLED', label: '支付部分取消' },
    // { value: 'PAYMENT_REFUNDED', label: '支付已退款' },
    // { value: 'PAYMENT_AUTHORIZED', label: '支付已授权' },
    // { value: 'PAYMENT_NOT_AUTH', label: '支付未授权' },
    { value: 'PAYMENT_SETTLED', label: '已支付' },
    // { value: 'PAYMENT_DECLINED', label: '支付已搁置' },
    { value: 'PAYMENT_REVERSED', label: '支付已冲正' },
    // { value: 'PAYMENT_REVERSED_UNKNOW', label: '支付冲正未知' },
    { value: 'PAYMENT_UNKNOW', label: '支付状态未知' },
    { value: 'ORDER_NOT_PAY', label: '未支付' },
  ];
  //订单交付状态	
  FulfillStatus = [
    { label: "请选择", value: "" },
    { value: 'FULFILL_CREATED', label: '未交付' },
    { value: 'FULFILL_COMPLETED', label: '已交付' },
    { value: 'FULFILL_FAILED', label: '交付失败' },
    { value: 'FULFILL_CANCELLED', label: '交付已取消' },
    // { value: 'FULFILL_REVERSED', label: '交付已冲正' },
    // { value: 'FULFILL_REVERSED_UNKNOW', label: '交付冲正未知' },
    { value: 'FULFILL_UNKNOW', label: '交付状态未知' },
  ];
  //日期
  ch: any = CALENDAR_CN;//讲英文转化为中文
  queryStartDate: Date = new Date();
  queryEndDate: Date = new Date();
  //校验
  userform: FormGroup;
  code: any;
  constructor(
    public orderHttpService: OrdersHttpService,
    public confirmationService: ConfirmationService,
    public commfunc: Commfunc,
    public router: Router,
    public fb: FormBuilder,
  ) {
    this.codeValues() //调用方法，获取全部码值
    console.log(this.code);
    this.bankNo = this.code['bankNo'];
    this.salesChannelEnumId = this.code['salesChannelEnumId'];
    this.tranType = this.code['tranType']
    this.orderStatusId = [
      { label: "请选择", value: "" },
      { label: "已创建", value: "ORDER_CREATED" },
      // { label: "处理中", value: "ORDER_PROCESSING" },
      { label: "已核准", value: "ORDER_APPROVED" },
      // { label: "已扣留", value: "ORDER_HOLD" },
      { label: "已完成", value: "ORDER_COMPLETED" },
      { label: "已取消", value: "ORDER_CANCELLED" },
      { label: "已拒绝", value: "ORDER_REJECTED" },
      { label: "已撤销", value: "ORDER_REVOKED" },
    ];
  }

  ngOnInit() {
    // this.queryClick();
    this.preSet();
    let _thiss = this;
    document.onkeydown = function (event) {
      if (event && event.keyCode === 13) {
        _thiss.queryClick();
      }
    }
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  openOrg(param) {
    this.orgTreeDisplay = !this.orgTreeDisplay;
    this.ownerOrgInValue = 'adjustOrg'
  }
  onHideClose() {
    this.personPage = false;
    this.orgTreeDisplay = false;
  }
  treeCall(param) {
    this.orgTreeDisplay = false;
    this.orderQueryBean.orderHeader.appBranchNo = param.orgId;
  }
  chooseWorker() {
    this.userInValue = '0000';
    this.personPage = !this.personPage;
  }
  perInfor(item) {
    this.personPage = false;
    if (item.length > 0) {
      this.orderQueryBean.orderHeader.appUserId = item[0].tellerId;
    }
  }
  preSet() {
    //表单校验
    this.userform = this.fb.group({
      'orderId': new FormControl('', Validators.compose([Validators.maxLength(20), specialSymbol])),
      'queryStartDate': new FormControl('', Validators.required),
      'queryEndDate': new FormControl('', Validators.required),
      'tradPartyId': new FormControl('', Validators.compose([specialSymbol])),
      'transactionId': new FormControl('', Validators.compose([specialSymbol])),
      'settleAcctNo': new FormControl('', Validators.compose([specialSymbol])),
      'userLoginId': new FormControl('', Validators.compose([specialSymbol])),
      'productId': new FormControl('', Validators.compose([specialSymbol])),
      'minAmt': new FormControl('', Validators.compose([specialSymbol])),
      'maxAmt': new FormControl('', Validators.compose([specialSymbol]))
    });
  }


  //查询
  queryClick(){
    this.pageSize = 10;
    this.pageNumber = 1;
    this.first = 0;
    //调用查询的方法
    this.queryOrderListPac();
  }
  
  queryOrderListPac(){
    //查询时间
    this.orderQueryBean.pageParam.pageSize = this.pageSize;
    this.orderQueryBean.pageParam.pageNumber = this.pageNumber;
    this.orderQueryBean.orderHeader.queryStartDate = this.commfunc.transDateN(this.queryStartDate);
    this.orderQueryBean.orderHeader.queryEndDate = this.commfunc.transDateN(this.queryEndDate);

    this.orderHttpService.queryOrderListPac(this.orderQueryBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.tableData = data.orderList;
        this.total = data.pageParam.totalCount;
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }
  //详情按钮事件
  dispatchPage(item) {
    this.router.navigate(['/pages/tzb/Trad/order-details'], { queryParams: { orderId: item, isext: 'N'} });
  }

  //分页
  paginate(event) {
    //每页显示的条数
    this.pageSize = event.rows * 1;
    //当前页
    let currentPage = event.page + 1;
    this.pageNumber = currentPage;
    this.first = event.page * event.rows;
    //调用查询的方法
    this.queryOrderListPac();
  }

  //重置
  resetClick() {
    this.orderQueryBean = new OrderQueryBean();
    this.queryStartDate = new Date();
    this.queryEndDate = new Date();
  }
  //日期范围的处理
  selectTime1() {
    this.selectTime(1);
  }
  selectTime2() {
    this.queryStartDate.getHours() == new Date().getHours() ? this.selectTime(2) : this.selectTime(1);
  }
  selectTime(a) {
    let startTime = this.queryStartDate.getTime();
    let endTime = this.queryEndDate.getTime()
    if (this.queryStartDate && this.queryEndDate) {
      if (this.queryStartDate > this.queryEndDate) {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
        setTimeout(() => {
          this.queryStartDate = null;
          this.queryEndDate = null;
        })
      } else if(endTime - startTime > (6 * 24 * 60 * 60 * 1000)){ //李艳 缺陷17133订单查询日期增加控制七天条件
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期与结束日期之间应小于7天' });
        setTimeout(() => {
          this.queryStartDate = null;
          this.queryEndDate = null;
        })
      }
    }
  }
}