import { inject } from '@angular/core/testing';
import { Injectable, NgModule } from '@angular/core';
import { API } from './order.http.constant';
import { HttpService } from 'app/@uisftech/common/service/http.service';
import { TZB_HTTP_PRO} from 'app/@uisftech/common/service/http.constant';

/**
 * 订单
 * 模块下的接口服务
 */
@Injectable()
export class OrdersHttpService {
    constructor(
        public httpService: HttpService
    ) {

    }
    // http://xwzhuat.tzbtest.ins/ywsn/
    //测试10.1.60.91
    Test(params) {
        params = this.httpService.handleParams(params);
        return this.httpService.post(TZB_HTTP_PRO + API.test, params);
    }
    queryOrderList(params) {
        params = this.httpService.handleParams(params);
        return this.httpService.post(TZB_HTTP_PRO + API.queryOrderList, params);
    }
    queryOrderBasicInfo(params) {
        params = this.httpService.handleParams(params);
        return this.httpService.post(TZB_HTTP_PRO + API.queryOrderBasicInfo, params);
    }
    revokeOrdersExt(params) {
        params = this.httpService.handleParams(params);
        return this.httpService.post(TZB_HTTP_PRO + API.revokeOrdersExt, params);
    }
    revokeOrderPac(params) {
        params = this.httpService.handleParams(params);
        return this.httpService.post(TZB_HTTP_PRO + API.revokeOrderPac, params);
    }
    transactionOrderExceptionHandling(params) {
        params = this.httpService.handleParams(params);
        return this.httpService.post(TZB_HTTP_PRO + API.transactionOrderExceptionHandling, params);
    }
    queryOrderListPac(params) {
        params = this.httpService.handleParams(params);
        return this.httpService.post(TZB_HTTP_PRO + API.queryOrderListPac, params);
    }
    //异常处理
    excOrderProcess(params) {
        params = this.httpService.handleParams(params);
        return this.httpService.post(TZB_HTTP_PRO + API.excOrderProcess, params);
    }
}