import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
//页面
import { TradComponent } from './Trad.component';//订单
import { DealCenterComponent} from './deal-center/deal-center.component'
import { ExceptionhandComponent } from './exception-hand/exception-hand.component';
import { OrderDetailsComponent } from './order-details/order-details.component';
import { DealCenterpacComponent } from './deal-centerpac/deal-centerpac.component';
const routes: Routes = [
    {
        path: '',
        component: TradComponent,
        children: [
            { path: '', redirectTo: 'deal-center', pathMatch: 'full' },
            // { path: 'order-center', loadChildren: './order-center/order-center.module#OrderCenterModule' },
            // { path: 'deal-center', loadChildren: './deal-center/deal-center.module#DealCenterModule' },
            { path: 'deal-center', component: DealCenterComponent },
            { path: 'exception-hand', component:ExceptionhandComponent},
            { path: 'order-details',component:OrderDetailsComponent},
            { path: 'deal-centerpac', component: DealCenterpacComponent },
            
        ]
    },
];

export const TradRouting:ModuleWithProviders  = RouterModule.forChild(routes);