import { Component, OnInit, Input } from '@angular/core';
// import { ApplyMessageBean } from './apply-message.bean';
// import { ParentChildCommunicationService } from '../common/service/parent-child-comm.service';
// import { CreditCcmService } from '../common/http/credit-ccm.http.service';
import { OrdersHttpService } from '../http/order.http.service';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { OrderDetailBean } from './order-details.bean';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Message } from 'primeng/primeng';
@Component({
  selector: 'app-order-details',
  templateUrl: './order-details.component.html',
  styleUrls: ['./order-details.component.scss'],
  providers: [Commfunc, OrdersHttpService]
})
export class OrderDetailsComponent implements OnInit {
  isPicInfo: boolean;//资产证明
  orderDetailBean: OrderDetailBean = new OrderDetailBean();
  orderId: any;
  ch: any = CALENDAR_CN;//讲英文转化为中文
  pageNumber = 1;
  pageSize = 10;
  code: any;
  paymentMethodId: any;
  paymentMethodTypeId: any;
  salesChannelEnumIdss = [];
  arrayList = [];
  arrayList1 = [];
  paymentStatus: any;
  isext: boolean = false;
  isextS: any;
  paymentStatuss = [
    { label: "请选择", value: "" },
    { label: "请选择", value: "" },
    // { value: 'PAYMENT_RECEIVED', label: '付款已收到' },
    // { value: 'PAYMENT_NOT_RECEIVED', label: '付款未收到' },
    { value: 'PAYMENT_CANCELLED', label: '支付已取消' },
    // { value: 'PAY_PART_CANCELLED', label: '支付部分取消' },
    // { value: 'PAYMENT_REFUNDED', label: '支付已退款' },
    { value: 'PAYMENT_AUTHORIZED', label: '支付已授权' },
    { value: 'PAYMENT_NOT_AUTH', label: '支付未授权' },
    { value: 'PAYMENT_SETTLED', label: '已支付' },
    // { value: 'PAYMENT_DECLINED', label: '支付已搁置' },
    { value: 'PAYMENT_REVERSED', label: '支付已冲正' },
    { value: 'PAYMENT_REVERSED_UNKNOW', label: '支付冲正未知' },
    { value: 'PAYMENT_UNKNOW', label: '支付状态未知' },
    // { value: 'ORDER_NOT_PAY', label: '未支付' },
  ]
  orderFulfillStatuss = [
    { label: "请选择", value: "" },
    { value: 'FULFILL_CREATED', label: '未交付' },
    { value: 'FULFILL_COMPLETED', label: '已交付' },
    { value: 'FULFILL_FAILED', label: '交付失败' },
    { value: 'FULFILL_CANCELLED', label: '交付已取消' },
    { value: 'FULFILL_REVERSED', label: '交付已冲正' },
    { value: 'FULFILL_REVERSED_UNKNOW', label: '交付冲正未知' },
    { value: 'FULFILL_UNKNOW', label: '交付状态未知' },
  ]
  orderStatusIdss = [
    { label: "请选择", value: "" },
    { label: "已创建", value: "ORDER_CREATED" },
    { label: "处理中", value: "ORDER_PROCESSING" },
    { label: "已核准", value: "ORDER_APPROVED" },
    { label: "已扣留", value: "ORDER_HOLD" },
    { label: "已完成", value: "ORDER_COMPLETED" },
    { label: "已取消", value: "ORDER_CANCELLED" },
    { label: "已拒绝", value: "ORDER_REJECTED" },
    { label: "已完成", value: "ORDER_APPROVED_FAIL" },
    { label: "已撤销", value: "ORDER_REVOKED" },
    { label: "状态未知", value: "ORDER_UN" },
  ];
  currencyUomIdss = [
    { label: "请选择", value: "" },
    { value: 'CNY', label: '人民币' },
    { value: 'USD', label: '美元' },
    { value: 'HKD', label: '港币' },
    { value: 'EUR', label: '欧元' },
    { value: 'JPY', label: '日元' },
    { value: 'GBP', label: '英镑' },
    { value: 'AUD', label: '澳大利亚元' },
    { value: 'CAD', label: '加拿大元' },
    { value: 'CHF', label: '瑞士法郎' },
    { value: 'DEM', label: '德国马克' },
    { value: 'SGD', label: '新加坡元' },
    { value: 'BEF', label: '比利时法郎' },
    { value: 'MOP', label: '澳门币' },
    { value: 'NLG', label: '荷兰盾' },
    { value: 'DKK', label: '丹麦克郎' },
    { value: 'SEK', label: '瑞典克朗' },
    { value: '98', label: '全外' },
    { value: '99', label: '全币种' },
    { value: 'NA', label: '缺省币种' },
  ];
  paymentMethodIds = [];
  paymentMethodTypeIds = [];
  orderTypeIds = [
    { label: "请选择", value: "" },
    { label: "销售订单", value: "SALES_ORDER" },
    { label: "采购订单", value: "PURCHASE_ORDER" },
  ];
  //提示信息
  msgs: Message[] = [];
  constructor(
    public orderHttpService: OrdersHttpService,
    public commfunc: Commfunc,
    public routerInfo: ActivatedRoute,
    public router: Router,
  ) {
    routerInfo.queryParams.subscribe(queryParams => {
      this.orderId = queryParams.orderId;
      this.isextS = queryParams.isext;
      this.arrayList = [];
      this.arrayList1 = [];
      this.paymentStatus = '';
      this.orderDetailBean = new OrderDetailBean();
    })
    if (this.orderId) {
      this.queryClick();
    }
  }

  ngOnInit() {
    if (this.isextS == 'Y') {
      this.isext = true;
    }
    this.isPicInfo = true;
    this.getCodeValues();
    this.salesChannelEnumIdss = this.code['salesChannelEnumId'];
  }
  getCodeValues() {
    this.commfunc.codeValue();
    const codeValues = this.commfunc.codeDatas;
    return this.code = codeValues;
  }
  gouback() {
    this.router.navigate(['/pages/tzb/Trad/deal-center']);
  }
  resetClick() {
    this.orderId = '';
  }
  deliveryOrder() {
    const pams = {
      orderId: this.orderId,
    }
    this.orderHttpService.excOrderProcess(pams).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }
  cancelOrder() {
    const pams = {
      orderId: this.orderId,
      orderDate: this.orderDetailBean.entryDate,
    }
    this.orderHttpService.revokeOrdersExt(pams).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }
  //获取详情信息
  queryClick() {
    const pams = {
      orderId: this.orderId,
      pageParam: {
        pageNumber: this.pageNumber,
        pageSize: this.pageSize,
      }
    }
    this.orderHttpService.queryOrderBasicInfo(pams).subscribe(data => {
      // if (data.returnCode.code == 'success') {
      // this.tableData = data.orderList;
      // this.total = data.pageParam.totalCount + '';
      this.orderDetailBean = data.orderHeader;
      this.orderDetailBean.entryDate = data.orderHeader.entryDate.substring(0, 10);
      this.arrayList = data.orderPayments;
      this.arrayList1 = data.orderItems;
      this.paymentStatus = data.orderPayments[0].paymentStatus
      this.msgs = [];
      this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }
}
